export default {
	data() {
		return {
			commdity: [],
			step: '',
			popupimg: '',
			total:'',
			acksite:true,
			acksite2:true,
			userName:'',
			telNumber:'',
			detailInfo:'',
			carmodel:''
		}
	},
	onLoad(option) {
		console.log(option)
		//默认地址
		this.userName = option.userName;
		this.telNumber = option.telNumber;
		this.carmodel = option.carmodel;
		this.detailInfo = option.detailInfo;
		//商品信息
		var commdity = uni.getStorageSync('comm');
		var step = uni.getStorageSync('steps');
		console.log(step)
		console.log(commdity)
		this.commdity = commdity
		this.step = step
		this.popupimg = commdity.slideshow[0].slideshow
		this.total = commdity.price * this.step
		console.log(this.total)
		
		if(this.userName !== ''){
			this.acksite = false;
			this.acksite2 = true
		}else{
			this.acksite2 = false;
			this.acksite = true
		}
	},
	methods: {
		site(){
			uni.navigateTo({
				url:'../site/site'
			})
		}
	}
}
